export default {
	data() {
		return {
			city: '武汉',
			columnData: [{
				url: '../../signUp/jiaxiao-ranking/jiaxiao-ranking',
				src: 'jkph',
				title: '驾校排行'
			}, {
				url: '../../signUp/Coach-ranking/Coach-ranking',
				src: 'jlph',
				title: '教练排行'
			}, {
				url: '../../signUp/Partner-service/Partner-service',
				src: 'plfw',
				title: '陪练服务'
			}, {
				url: '../../signUp/Driving-test-guide/Driving-test-guide',
				src: 'jkzn',
				title: '驾考指南'
			}, ],
			listData: [],
			adData: []
		}
	},
	onLoad: function(options) {

		/* 获取定位 */



















































		//驾校列表
		uni.request({
			url: this.$Url + '/api/school/home',
			method: 'GET',
			data: {},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					var arr = []
					for (let i in res.data.msg) {
						res.data.msg[i].coverImg = this.$Url + res.data.msg[i].coverImg
						res.data.msg[i].label = res.data.msg[i].label.split(",")
						res.data.msg[i].score = res.data.msg[i].score == null ? 5 : res.data.msg[i].score
						arr.push(res.data.msg[i]); //属性
					}
					console.log(arr)
					this.listData = arr
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});
		//广告
		uni.request({
			url: this.$Url + '/api/car/ad',
			method: 'GET',
			data: {
				position: 'home'
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					console.log(res.data)
					let arr = []
					for (let i in res.data.msg) {
						res.data.msg[i].image = this.$Url + res.data.msg[i].image
						arr.push(res.data.msg[i]);
					}
					console.log(arr)
					this.adData = arr
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});

	},
	onShow: function() {

	},
	onReachBottomDistance(e) {
		console.log(e)
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		openBrowser: function(e) {
			plus.runtime.openURL(e.currentTarget.dataset.url)
		}
	}
}
